;(function ($, APP) {
  APP.back = {}

  function init () {
    console.log('init back')
    var cont = $('#back')
    var front = $('#front')
    var svg = $('#back svg')
    var impe

    var onClicked

    var elements = svg.find('#uomini > image')

    elements.on('click', function (e) {
      onClicked()
    })

    var width_view = $('body').width()
    var width_content = $('#back').width()

    impe = new Impetus({
      source: '#back',
      boundX: [(width_content - width_view) * -1, 0],
      bounce: true,
      initialValues: [0, 0],
      update: function (x, y) {
        cont.css({x: x})
        front.css({x: x * 1.65})
      }
    })

    $(window).on('resize', function () {
      console.log('resize')
      width_view = $('body').width()
      width_content = $('#back').width()

      impe.setBoundX([(width_content - width_view) * -1, 0])

      console.log((width_content - width_view) * -1)

      restore()
    })

    function restore (_d) {
      var d = _d || 0
      width_view = $('body').width()
      width_content = $('#back').width()
      cont.css({x: ((width_content - width_view) * -1)})
      front.css({x: ((width_content - width_view) * -1) * 1.65})
      impe.setValues(((width_content - width_view) * -1), 0)
    }

    function begin (picked) {
      impe.pause()
      elements.css('display', 'none')
      $(elements.get(picked)).css('display', 'block')

      impe.setValues(((width_content - width_view) * -1) / 2, 0)

      front.transition({x: (width_content - width_view) / 2 * -1 * 1.65, delay: 100}, 2500, 'easeInOutQuart')
      cont.transition({x: (width_content - width_view) / 2 * -1,
        delay: 100,
        complete: function () {
          impe.resume()
        }}, 2500, 'easeInOutQuart')
    }

    function pick (picked) {
      elements.css('display', 'none')
      $(elements.get(picked)).css('display', 'block')
    }

    function show () {
      cont.show()
    }

    function hide () {
      cont.hide()
    }

    APP.back.onClicked = function (clb) {
      onClicked = clb
    }

    APP.back.elements = elements
    APP.back.restore = restore
    APP.back.begin = begin
    APP.back.pick = pick
    APP.back.hide = hide
    APP.back.show = show
  }

  APP.back.init = init

  $(document).ready(function () {
    if (APP.version === undefined && $('#back')[0]) {
      setTimeout(function () {
        init()
      }, 500)
    }
  })
})(window.jQuery, window.APP)
