window.APP = {version: 'window.version'}

;(function ($, APP) {
  APP.game = new GamePlay()

  APP.getDato = function () {
    var dato = null
    var name = APP.game.getCurrentName()
    APP.db.forEach(function (d) {
      var n = d.layer_name.replace(/_\d_/g, '')
      console.log(n, name)
      if (n === name) dato = d
    })
    return dato
  }

  APP.country = 'us'
  $.get('http://ip-api.com/json', function (data) {
    console.log('ip-api.com', data)
    APP.country = (data.countryCode) ? data.countryCode.toLowerCase() : 'us'
  })

  $(document).ready(function () {
    $('svg').each(function (i, e) {
            // fix id name
            // sandbox ref id

      var elemns = $(e).find('[id]')
      var clips = $(e).find('[clip-path^="url"]')
      var fills = $(e).find('[fill^="url"]')
      var strokes = $(e).find('[stroke^="url"]')
      console.log(elemns.length, clips.length, clips.length, strokes.length)
    })
  })
})(window.jQuery, window.APP)
